.Ccon{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: burlywood;
    gap: 20px;
}
.Ccard{
    width: 30%;
    border-radius: 18px;
    background-color: beige;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    gap: 20px;
    text-align: center;
    padding: 20px 10px;
}
.Cimg{
    width: 100px;
    height: 100px;
}
.Cimg img{
    width: 100%;
    height: 100%;
    border-radius: 40%;
}
.Ctotal{
    width: 100%;
    height: 5vh;
    font-size: 32px;
    text-align: center;
    line-height: 3;
}
.Ccard button{
    width: 75px;
    height: 30px;
}
.Ccard div button{
    width: 20px;
    font-size: 18px;
    margin: 5px;

}
@media screen and (max-width:768px) {

    .Ccard{
        width: 40%;
    }
    
}