.Hcon{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: burlywood;
    gap: 20px;
}
.Hcard{
    width: 30%;
    background-color: beige;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    gap: 20px;
    border-radius: 18px;
    text-align: center;
    padding: 20px 10px;
}
.Himg{
    width: 100px;
    height: 100px;
}
.Himg img{
    width: 100%;
    height: 100%;
    border-radius: 40%;
}
*{
    margin: 0px;
    padding: 0px;
    outline: none;
}
.Hcard button{
    width: 100px;
    height: 40px;
    background-color: black;
    color: crimson;
    border-radius: 10px;
    font-size: 16px;
}
@media screen and (max-width:768px) {

    .Hcard{
        width: 40%;
    }
    
}